@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Ubuntu+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Ubuntu+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Ubuntu+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background: black
}
@media screen and (min-width: 690px) {
  body {
    background: url('https://miro.medium.com/max/3840/1*PyExFMlUFx1Yv_28Nd1BRA.png');
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  min-height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



.timeline-fakebox{height:80px}.timeline{background:black;padding-left:0;margin-left:0;max-width:100%}.vertical-timeline-element{font-family:'Ubuntu Mono', monospace;color:#949494;margin-bottom:7rem;padding:0.1rem;transition:500ms}.vertical-timeline-element-content{display:flex;flex-direction:column;box-shadow:1px 0px 5px 5px rgba(255,255,255,0.5) inset;padding:0.3rem;background-color:#000;transition:500ms}.vertical-timeline-element-content:hover{color:white;transition:500ms}.vertical-timeline-element-title{text-align:left;margin-bottom:1rem;width:100%;text-decoration:underline}.vertical-timeline-element-desc{text-align:left;padding:0.2rem;border-top:1px solid grey;border-bottom:1px solid grey}.vertical-timeline-element-image{max-width:200px;max-height:100px;align-self:center}.vertical-timeline-element-techs{display:flex;margin:0;margin-bottom:0.5rem;padding:0;border-top:1px solid grey;border-bottom:1px solid grey;width:100%;overflow:hidden}.vertical-timeline-element-techs__title{text-align:left;padding:0;margin:0}.vertical-timeline-element-techs__button{border:3px solid #00c2fd;box-shadow:1px 0px 1px 1px rgba(0,0,0,0.5) inset;border-radius:10px;padding:0;margin:0.3rem;max-height:40px}.vertical-timeline-element-techs__button--text{padding:0.2rem;margin:0.2rem}.vertical-timeline-element--work{max-height:400px}.thumbnail iframe{width:1440px;height:900px}.thumbnail{max-height:250px;position:relative;-ms-zoom:0.25;-moz-transform:scale(0.25);-moz-transform-origin:0 0;-o-transform:scale(0.25);-o-transform-origin:0 0;-webkit-transform:scale(0.25);-webkit-transform-origin:0 0}@media screen and (min-width: 690px){.timeline{background:transparent}}@media screen and (min-width: 1170px){.timeline{width:100%}.vertical-timeline-element-image{max-width:400px;max-height:200px;align-self:center}}

.navbar{font-family:'Russo One', sans-serif;display:none;flex-direction:row;justify-content:space-between;background-color:black;height:80px;top:0;min-width:100%;position:sticky;z-index:1;box-shadow:1px 9px 21px 0px rgba(255,243,243,0.75);opacity:0.9;transition:500ms;margin:0;padding:0}.navbar:hover{opacity:1}.navbar__logo{align-self:center;max-height:80px;color:white;cursor:pointer;margin-left:1rem;transition:500ms}.navbar__logo:hover{border-radius:50px;background-color:rgba(104,104,104,0.556);transition:500ms}.navbar--scrolled{background:#000;opacity:0.5;transition:500ms}.navbar-right{margin:0.65rem;margin-right:1rem}.navbar-right>a:hover{fill:blue}.navbar-middle{margin:1rem;font-size:2rem}.navbar-middle>a{text-decoration:none;color:white;margin:1rem;transition:500ms;cursor:pointer}.navbar-middle>a:hover{color:#00c2fd;transition:500ms}.navbar-mobile{display:flex;justify-content:space-evenly;align-items:center;position:fixed;bottom:0;left:0;z-index:1;color:white;height:70px;width:100vw;background-color:black;box-shadow:1px 9px 21px 0px rgba(255,243,243,0.75);padding:0;margin:0;font-size:3rem}.navbar-mobile__icon{text-align:center;height:70px;margin-top:1rem;cursor:pointer}.navbar-test{height:100vh}@media screen and (min-width: 690px){.navbar{display:flex}}

.contact{position:fixed;background:#00000050;width:100%;height:100vh;top:0;left:0}.contact a{text-decoration:none;color:white}.contact-box{position:relative;width:80%;margin:0 auto;height:auto;max-height:70vh;margin-top:calc(100vh - 70vh - 5px);background:black;overflow:auto;border-radius:25px;border:5px double white;padding:1rem}.contact-box__close-icon{content:'x';cursor:pointer;position:fixed;right:calc(10% - 30px);top:calc(100vh - 70vh - 33px);background:black;width:25px;height:25px;border-radius:50%;line-height:20px;text-align:center;border:3px solid white;font-size:20px}.contact-box__close-icon:hover{color:#00c2fd;border-color:#00c2fd}

